@use "../../const";
@use "../../palette";

.strips {
    margin: -1rem -1.25rem -1rem;
    border-top: 1px solid var(--sleek-surface-border-color-subtle);

    &__item {
        display: block;
        position: relative;
        padding: 0.5rem 1.25rem;
        border-bottom: 1px solid var(--sleek-surface-border-color-subtle);
        text-decoration: none;
        color: var(--sleek-text-default-color);
        transition: background-color const.$fast ease-in;

        &:focus,
        &:hover {
            text-decoration: none;
            color: var(--sleek-text-default-color);
            box-shadow: inset 0.5rem 0 0 -0.25rem palette.$blueglow;
        }

        &:last-child {
            border-bottom: none;
        }

        &.-active {
            box-shadow: inset 0.5rem 0 0 -0.25rem palette.$blue;

            &:last-child {
                border-radius: 0 0 0 0.25rem;
            }
        }

        &.-disabled {
            color: palette.$muted;
            background-color: transparent;
            cursor: default;
            opacity: 0.75;
        }

        .icon {
            margin-top: -0.125rem;

            + .text {
                margin-left: 0.5rem;
            }
        }

        .text + .icon {
            margin-left: 0.5rem;
        }
    }
}

@media (max-width: 991px) {
    .strips {
        > :not(.-active),
        .badge {
            display: none;
        }
    }

    .stripsbox {
        display: inline-block;
        margin-bottom: 0.5em;

        > .panel {
            display: none;
        }
    }
}
